<template>
  <div class="body">
    <van-nav-bar title="" left-arrow @click-left="onClickLeft" />
    <div class="container">
      <div class="title">
        <h3></h3>
        <p></p>
      </div>

      <div class="form">
        <div class="form-item">
          <input v-model="mobile" class="inp" maxlength="11" placeholder="" type="text">
        </div>
        <div class="form-item">
          <input v-model="picCode" class="inp" maxlength="5" placeholder="" type="text">
          <!--  -->
          <img v-if="picUrl" :src="picUrl" @click="getPicCode" alt="">
        </div>
        <div class="form-item">
          <input v-model="msgCode" class="inp" placeholder="" type="text">
          <button @click="getCode">
            <!--  -->
            {{ second === totalSecond ? '' : second + '' }}
          </button>
        </div>
      </div>

      <div @click="login" class="login-btn"></div>
    </div>
  </div>
</template>

<script>
import { getMsgCode, getPicCode, msgLogin } from '@/api/login'
export default {
  name: 'myLogin',
  async created() {
    this.getPicCode()
  },
  destroyed() {
    //
    clearInterval(this.timer)
  },
  data() {
    return {
      mobile: '',//
      picKey: '',//
      picCode: '',//
      second: 10,//
      totalSecond: 10,
      msgCode: '',//
      picUrl: '',//urlbase64
      timer: null//
    }
  },
  methods: {
    onClickLeft() {
      this.$router.back()
    },
    async getPicCode() {
      const res = await getPicCode()
      this.picUrl = res.data.base64
      this.picKey = res.data.key
    },
    async getCode() {
      if (!this.isValid()) {
        return
      }
      if (this.timer) {
        return
      }
      const res = await getMsgCode(this.picCode, this.picKey, this.mobile)
      this.$toast("")
      //
      this.timer = setInterval(() => {
        this.second--
        if (this.second === 0) {
          clearInterval(this.timer)//
          this.timer=null//
          this.second = this.totalSecond
        }
      }, 1000)
    },
    //
    isValid() {
      //
      if (this.mobile.length != 11) {
        this.$toast('')
        return false
      }
      //
      if (!/^\w{4}$/.test(this.picCode)) {
        this.$toast('')
        return false
      }
      return true
    },
    async login() {
      //
      if (!this.isValid()) {
        return
      }
      //
      if (this.msgCode!=='246810') {
        this.$toast('')
        return
      }
      const res = await msgLogin(this.mobile, this.msgCode)
      this.$toast("")
      this.$store.commit('user/setUserInfo', res.data)
      const url = this.$route.query.backUrl || '/'
      //
      //
      this.$router.replace(url)
    }
  }
}
</script>

<style scoped lang="less">
.body {
  width: 400px;
  margin: 0 auto;
  margin-top: 30px;
}

.container {
  padding: 49px 29px;

  .title {
    margin-bottom: 20px;

    h3 {
      font-size: 26px;
      font-weight: normal;
    }

    p {
      line-height: 40px;
      font-size: 14px;
      color: #b8b8b8;
    }
  }

  .form-item {
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;

    .inp {
      display: block;
      border: none;
      outline: none;
      height: 32px;
      font-size: 14px;
      flex: 1;
    }

    img {
      width: 94px;
      height: 31px;
    }

    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }

  .login-btn {
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg, #ecb53c, #ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0, 0, 0, .1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>