import { setUserInfo, getInfo, removeInfo } from '@/utils/storage'
export default {
  namespaced: true,
  state: {
    //info
    //  info  info  Vuex store  getInfo 
    info: getInfo()
  },
  getters:{
    token(state){
      return state.info.token
    }
  },
  mutations: {
    setUserInfo(state, info) {
      state.info = info
      //
      setUserInfo(info)
    }
  }
}