<template>
  <div class="order">
    <van-nav-bar title="" left-arrow @click-left="$router.go(-1)" />
    <van-tabs v-model="active" >
      <van-tab name = "all" title=""></van-tab>
      <van-tab name = "payment" title=""></van-tab>
      <van-tab name = "delivery" title=""></van-tab>
      <van-tab name = "received" title=""></van-tab>
      <van-tab name = "comment" title=""></van-tab>
    </van-tabs>

    <OrderListItem v-for="item in orderList" :key = "item.order_id" :item="item"></OrderListItem>
  </div>
</template>

<script>
import OrderListItem from '@/components/OrderListItem.vue'
import { getOrderList } from '@/api/order';
import { Tab ,Tabs} from 'vant';
export default {
  name: 'OrderPage',
  components: {
    OrderListItem,[Tab.name]:Tab,[Tabs.name]:Tabs
  },
  data () {
    return {
      active: this.$route.query.dataType ||'all',
      orderList:[]
    }
  },
  methods:{
    async getOrderList(){
      const res = await getOrderList(this.active,1)
      //
      this.orderList = res.data.list.data
      this.orderList.forEach(item=>{
        item.total_num = item.goods.reduce((sum,cur)=>{
          return sum+cur.total_num
        },0)
      })
      // console.log(this.orderList)
    }
  },
  watch:{
    active:{
      //handler
      immediate:true,
      //active
      handler () {
        this.getOrderList()
      }
    }
  }
}
</script>

<style lang="less" scoped>
.order {
  background-color: #fafafa;
}
.van-tabs {
  position: sticky;
  top: 0;
}
</style>