//
export default {
  methods: {
    // 
    // (1)  true
    // (2)  false
    loginConfirm() {
      if (!this.$store.getters['user/token']) {
        this.$dialog.confirm({
          title: '',
          message: '',
          confirmButtonText: '',
          cancelButtonText: ''
        })
          .then(() => {
            //  =>  ()
            // this.$route.fullPath ()
            this.$router.replace({
              path: '/login',
              query: {
                backUrl: this.$route.fullPath
              }
            })
          })
          .catch(() => { })
        return true
      }
      return false
    }
  }
}
