import Vue from 'vue'
import Vuex from 'vuex'
import user from '@/store/modules/user'
import cart from '@/store/modules/cart'
//
Vue.use(Vuex)
//Store
export default new Vuex.Store({
  state: {
    ///
    //selectAddress
    //index
    index: 0
  },
  mutations: {
    setIndex: (state, index) => {
      state.index = index
    }
  },
  modules: {
    user, cart
  }
})
